.group-info-container{
    background: #FFF9E3;
    border-radius: 21px;
    padding:20px;
  }
  .group-info-container-sky{
    background: rgba(78, 143, 136, 0.05);
    border-radius: 21px;
    padding:20px;
  }

.group-info-container-inner-box{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    background: #FFFFFF;
    width: 100%;
    min-height:190px;
}
/* 가로라인  */
.group-line{
    margin: 0px 10px 0px 10px ;
    height: 1px;
    background-color:rgba(0, 0, 0, 0.15);
}

.group-add-header{ 
    background: #573300;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
}

.group-button-bottom{
    display: inline-block;
    width: 380px;
    height: 46px;
    background: #573300;
    border-radius: 5px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 19px;
    line-height: 46px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #FFFFFF;
    margin-left: 0px;
    margin-top: 10px;
}

.group-add-step1-menu{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;  
    align-items: center;
    justify-content: space-around;
    /* height: 46px; */
    margin-left: 20px;
    margin-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    background: #FFFFFF;
    border: 1px solid #FFBF26;
    box-sizing: border-box;
    border-radius: 30px;
    
    /* width: 400px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;  */
}

/* 스크롤 */
.common-row-scroll{
    margin: 4px, 4px;
    padding: 4px;
    width: 500px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
}

.group-title-box{
    background: #FFEFCB;
    border: 2px solid #573300;
    box-sizing: border-box;
    border-radius: 14px;
}

.group-content{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
} 

.group-list-input-box{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;  
    align-items: center;
    justify-content: space-around;
    background: #ECECEC;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 14px;
    padding-top: 30px;
    padding-bottom: 30px;
}

.group-grey-container{
    background: #eee3e3;
    border-radius: 14px;
}
.group-white-container{
    background: #ffffff;
}
.group-slider-container{
    margin: 0px 0px 0px 0px;
}
.group-card-title{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    background: rgba(255, 255, 255, 0.85);
} 

.span-group-quote{
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: bold;
    font-weight: 550;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #573300;
    padding-top: 6px;
    padding-left: 6px;
    padding-right: 6px;
} 