/* search */
.search{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    height: 50px;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 25px;
}
.search-input{
    margin-left: 10px;
}
.search-button{
    margin-right: 10px;
}

/* 더보기 긴 버튼 */
.item-add-basci{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 21px;
    background: #573300;
    border-radius: 5px;
}
